
<template>
  <Details>
      <div  id='write'  class = 'is-node'><h1><a name='header-n0' class='md-header-anchor '></a>多人协同开发产生冲突问题</h1><h3><a name='header-n11' class='md-header-anchor '></a>a和b两人开发</h3><ul><li>a和b两人同时git clone下载文件修改，或者a先创建文件上传后(比如index.html文件)。</li><li>然后a和b同时修改index.html文件</li><li>此时如果b优先上传更新了index.html文件，然后a也提交上传，就会出现文件冲突</li><li>a上传时，git会自己尝试合并，合并会失败，git不能自动合并冲突，但是git会提示你更新代码，更新代码的时候，会告诉你冲突的位置，这个时候就需要自己去手动编辑改冲突文件，然后重新提交就可以了。</li></ul></div>
  </Details>
</template>
<script>
export default {
  name:'git_morekf',
}
</script>


